.select-box li {
    position: relative;
    display: inline-block;
    float: left;
    margin-right: 24px;
}
.select-box .single-checkbox {
    position: relative;
    display: block;
}
.select-box input[type="radio"] {
    display: none;
}

input[type="radio"], input[type="checkbox"] {
    box-sizing: border-box;
    padding: 0;
}

.select-box label {
    position: relative;
    display: block;
    padding-left: 20px;
    margin-right: 0px;
    margin-bottom: 0;
    color: #061a3a;
    font-size: 17px;
    line-height: 30px;
    font-weight: 500;
    cursor: pointer;
}

.select-box input[type="radio"]:checked + label span {
    border-color: #39cabb;
}
.select-box input[type="radio"] + label span {
    position: absolute;
    top: 10px;
    left: 0;
    width: 10px;
    height: 10px;
    vertical-align: middle;
    background-color: transparent;
    border: 2px solid #061a3a;
    border-radius: 50%;
    cursor: pointer;
    transition: all 300ms ease;
}

.select-box label span:before {
    background-color: #39cabb;
}

.select-box label span:before {
    position: absolute;
    content: '';
    width: 8px;
    height: 8px;
    left: -1px;
    top: -1px;
    border-radius: 50%;
    opacity: 0;
    transform: scale(0,0);
    transition: all 500ms ease;
}
.select-box input[type="radio"]:checked + label span:before {
    opacity: 1;
    transform: scale(1,1);
}

.select-box li:last-child {
    margin: 0px !important;
}

