.elementor-section {
    position: relative;
}
.elementor-section .aaa{
    max-width: 1140px;
}
.elementor-section .elementor-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-right: auto;
    margin-left: auto;
    position: relative;
}
.elementor-column, .elementor-column-wrap {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.elementor-column {
    min-height: 1px;
}
.elementor:not(.elementor-bc-flex-widget) .elementor-widget-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.elementor-widget-wrap {
    position: relative;
    width: 100%;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-line-pack: start;
    align-content: flex-start;
}
.elementor-widget-wrap>.elementor-element {
    width: 100%;
}
.elementor-element .elementor-widget-container {
    -webkit-transition: background .3s,border .3s,-webkit-border-radius .3s,-webkit-box-shadow .3s;
    transition: background .3s,border .3s,-webkit-border-radius .3s,-webkit-box-shadow .3s;
    -o-transition: background .3s,border .3s,border-radius .3s,box-shadow .3s;
    transition: background .3s,border .3s,border-radius .3s,box-shadow .3s;
    transition: background .3s,border .3s,border-radius .3s,box-shadow .3s,-webkit-border-radius .3s,-webkit-box-shadow .3s;
}
.about-section {
    position: relative;
    padding: 110px 0px 120px 0px;
}
.image_block_1 .image-box {
    position: relative;
    display: block;
    max-width: 570px;
}
.image_block_1 .image-box:before {
    position: absolute;
    content: '';
    background: #b7e9e1;
    width: 400px;
    height: 400px;
    border-radius: 50%;
    left: 5px;
    top: 150px;
}
.image_block_1 .image-box .pattern .pattern-1 {
    position: absolute;
    right: 120px;
    top: 105px;
    width: 560px;
    height: 491px;
    z-index: -1;
    background-repeat: no-repeat;
}
.image_block_1 .image-box .pattern .pattern-2 {
    background: #39cabb;
}
.image_block_1 .image-box .pattern .pattern-2 {
    position: absolute;
    left: 0px;
    top: 170px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    opacity: 0.7;
    -webkit-animation: zoom-fade 3s infinite linear;
    animation: zoom-fade 3s infinite linear;
}
.image_block_1 .image-box .pattern .pattern-3 {
    position: absolute;
    top: 167px;
    right: 168px;
    width: 124px;
    height: 144px;
    z-index: 1;
    background-repeat: no-repeat;
}
.image_block_1 .image-box .image img {
    position: relative;
    max-width: none;
    float: right;
}
.elementor img {
    height: auto;
    max-width: 100%;
    border: none;
    -webkit-border-radius: 0;
    border-radius: 0;
    -webkit-box-shadow: none;
    box-shadow: none;
}
.elementor:not(.elementor-bc-flex-widget) .elementor-widget-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

section.about-section.two {
    padding-top: 200px;
}
.about-section .content_block_1 .content-box {
    margin-left: 50px;
}
.content_block_1 .content-box .sec-title {
    margin-bottom: 24px;
}
.sec-title p {
    color: #39cabb;
}
.sec-title p {
    position: relative;
    display: inline-block;
    font-size: 17px;
    line-height: 26px;
    font-weight: 600;
    text-transform: uppercase;
    padding-bottom: 17px;
}
.sec-title h2 {
    position: relative;
    display: block;
    font-size: 50px;
    line-height: 55px;
    font-weight: 900;
}
.content_block_1 .content-box .text {
    margin-bottom: 24px;
}
p {
    position: relative;
    font-family: 'Poppins', sans-serif;
    color: #808080;
    font-weight: 400;
    margin: 0px;
    transition: all 500ms ease;
}
.content_block_1 .content-box .list-style-one {
    margin-bottom: 34px;
}
.list-style-one li {
    position: relative;
    display: block;
    font-size: 18px;
    line-height: 26px;
    font-family: 'Jost', sans-serif;
    color: #00224f;
    font-weight: 500;
    margin-bottom: 8px;
    padding-left: 30px;
    display: block;
}
.list-style-one li:before {
    border: 3px solid #39cabb;
}
.list-style-one li:before {
    position: absolute;
    content: '';
    width: 14px;
    height: 14px;
    left: 0px;
    top: 6px;
    border-radius: 50%;
}
.elementor a {
    -webkit-box-shadow: none;
    box-shadow: none;
    text-decoration: none;
}
.theme-btn-one {
    position: relative;
    display: inline-block;
    font-size: 15px;
    line-height: 26px;
    font-weight: 600;
    color: #fff !important;
    text-align: center;
    padding: 17px 40px;
    border-radius: 30px;
    z-index: 1;
    box-shadow: 0 20px 30px #d5edea;
    transition: all 500ms ease;
}

